.projects__showcase-item {
    position: relative;
    margin: 10% auto 0;
    max-width: 800px;
    width: 100%;
    height: 100%;
    figure {
        overflow: hidden;
        z-index: 0;
        img {
            display: block;
            height: auto;
            width: 100%;
            transform: scale(1);
            transition: 200ms ease-in-out;
        }
    }
    &:hover {
        h1 {
            transform: scale(1.05);
            margin-bottom: 2px;
        }
        p {
            transform: scale(1.05);
        }
        img {
            transform: scale(1.05);
        }
    }
    color: #fff;
    .projects__showcase-item-overlay {
        @include flexCenter(column);
        justify-content: flex-end;
        padding-bottom: 15%;
        background-color: rgba($color: #000000, $alpha: 0.6);
        height: 100% !important;
        width: 100% !important;
        position: absolute;
        width: 60%;
        z-index: 1;
        h5 {
            position: absolute;
            text-transform: uppercase;
            top: 4rem;
            left: 2rem;
        }
        h1 {
            font-size: 2.5rem;
            transition: 200ms ease-in-out;
        }
        p {
            width: 70%;
            text-align: center;
            margin-bottom: 1rem;
            transition: 200ms ease-in-out;
        }
        .projects__showcase-item-buttons-ctn {
            @include flexCenter(row);
            width: 50%;
            a {
                @include anchorBtn();
                text-align: center;
                font-size: 1rem;
                background-color: rgba($color: $darkpurple, $alpha: 0.5);
                border-color: rgba($color: $lightorange, $alpha: 0.5);
                margin: 1rem;
                padding: 0.5rem;
                flex: 1;
                svg {
                    vertical-align: bottom;
                    font-size: 1.3rem;
                }   
            }
        }
    }

}

@media screen and (max-width: 1000px) {
    .projects__showcase-item {
        margin-bottom: 150px;
        .projects__showcase-item-overlay {
            justify-content: center;
            padding-bottom: 0;
            h5 {
                font-size: 3vw;
                position: relative;
                top: 0;
                left: 0;
            }
            h1 {
                font-size: 8vw;
            }
            p {
                display: none;
            }
            .projects__showcase-item-buttons-ctn {
                position: absolute;
                bottom: -150px;
                flex-direction: column;
                width: 80%;
                a {
                    width: 100%;
                    margin: 0.5rem 0 0;
                    padding: 1rem;
                }
            }
            &:hover {
                h1 {
                    font-size: 8.5vw;
                }
            }
        }
    }
}